import React, { useState } from "react";
import {send} from "emailjs-com"
import "./contact.css";

export default function Contact() {

    const display = document.getElementById("pop-up")
    const [popUp, setPopUp] = useState(false)
    const [contactForm, setContactForm] = useState({
        name: "",
        email: "",
        message: ""
    })

    const handleFormChange = (event) => {
        setContactForm(prevFrom => ({
            ...prevFrom,
            [event.target.name]: event.target.value
        }))
    }


    const handleSubmit = (event) => {
        event.preventDefault()

        function clearForm () {
            setContactForm(prevForm => ({...prevForm,
                name: "",
                email: "",
                message: ""
            }))
        }

        send (
            'service_mwenlo9',
            'template_l8rsvl8',
            contactForm,
            'eRpiSNMtmHHuJFjP0'
            ).then(function() {
                clearForm()
                setPopUp(!popUp)
                
            }).catch(() => {
                let errorMess = "Sorry. Something must've gone wrong. Please try again."
                display.textContent = errorMess
                return errorMess
            })
        
    }

    if (popUp) {
        display.textContent = ""
    }

    return (
        <div className="contact" id="contacted">
            <div className="contact-sections">
                <div className="contact-left">
                    <div className="contact-title">
                    <h2>Contact</h2>

                    </div>
                    <form onSubmit={handleSubmit}>
                        <div className="input-wrapper">
                            <input className="input"
                                type="text"
                                placeholder="Name"
                                name="name"
                                value={contactForm.name}
                                onChange={handleFormChange}
                                required />
                        </div>
                        <div className="input-wrapper">     
                            <input className="input"
                                type="email"
                                placeholder="Email"
                                name="email"
                                value={contactForm.email}
                                onChange={handleFormChange}
                                required />
                        </div>
                        <div className="input-wrapper">
                            <textarea className="input"
                                placeholder="Message"
                                name="message"
                                value={contactForm.message}
                                onChange={handleFormChange}
                            />
                        </div>
                        <div className="btn-wrapper">
                            <button className="form-btn">Submit</button>
                            </div>
                    </form>
                    <div><p id="pop-up"></p></div>
                    </div>
                <div className="contact-right">
                    {popUp ?
                        <div className="success-text">
                            <h3>
                                Thank you for submitting your message. We'll get back to you soon.
                            </h3>
                    </div>
                     : 
                        <div className="default-text">
                            <h3>
                                Don't be a stranger. Get in touch!
                            </h3>
                    </div>
                    }
                </div>
            </div>
        </div>
    )
}