import "./about.css"
import React from "react"
import astronaut from "../images/astronaut-mid.png"
import Skills from "./Skills"

export default function About() {


    return (
        <div className="about" id="about-me">
            <h2><span>AB</span>OUT ME</h2>
            <div className="about-divs">
                <div className="about-left">
                    <img className="left-img" src={astronaut} alt="astronaut" />
            </div>
            <div className="about-right">
                    <p>
                        I work as a freelance front end developer for individauls and small businesses and I've been doing that for quite some time now. One of the things I love about what I do is seeing the end result of what was once an abstract idea, watching that idea pieced into parts and come together. <br/>
                        I am learning to code on my own so I have to work hard to
                        stay motivated and keep learning. Nevertheless, I'm determined to keep challenging myself.
                        </p><p>
                        Outside of all things front end, I do some art and one day when some new invention overthrows the internet in popularity, I hope to sell them and become rich.
                        </p>
                </div>
                </div>
                <Skills />

        </div>
    )
}