import React, { useEffect, useState } from "react";
import { db } from "../FirebaseConfig"
import { getDocs, collection } from "firebase/firestore";

import "./projectFeed.css"

export default function ProjectFeed() {
    const [postLists, setPostList] = useState([]);
  const postCollectionRef = collection (db, "projects");

    useEffect(() => {
        const getPosts = async () => {
        const data = await getDocs(postCollectionRef);
        setPostList(data.docs.map((doc) => ({...doc.data(), id: doc.id})));
        };
        getPosts();
    });

    const feedElements = postLists.map((item)=>{
        let path = item.link

        const livePageRedirect = () => {
            window.open(path)
        }  

        return (
            <div className="homeFeed-block">
                <div className="feed-img-container">
         <img className="homeFeed-img" src={item.image} alt={item.alt}/></div>
                {/*<img className="project-img" src={project.image} alt={project.alt}/>*/}
                <div className="project-text">
                <h3 className="homeFeed-title" >{item.title}</h3>
                <p className="feed-description">
                    {item.description}
                    </p>
                    <div className="btn-wrapper">
                        <button onClick={livePageRedirect} className="redirect-btn" >View Project</button>
                        </div>
                    </div>
    </div>
     )}
     )

    return (
        <div className="project-feed">
            <div className="project-header">
                <h2>Projects</h2>
            </div>
            <div className="project-grid">
                {feedElements}
            </div>
        </div>
    )
}