import React from "react";

import hero from "../images/hero.png"
import Header from "../components/Header";
import "./portfolio.css"
import About from "../components/About";
import Contact from "../components/Contact";
import Footer from "../components/Footer";
import ProjectHomeFeed from "../components/ProjectHomeFeed";

export default function Portfolio() {

    const scrollon = () => {
        document.querySelector('#contacted').scrollIntoView({ 
            behavior: 'smooth' 
          });
    }

    const scroll = () => {
        document.querySelector('#about-me').scrollIntoView({ 
            behavior: 'smooth' 
          });
    }

    return (
        <div className="portfolio">
            <div className="port-div">
            <Header />
            <div className="hero">
                <div className="hero-text">
                    <div className="main-text-wrapper">
                        <div className="main-text">
                            <h1>SYNTICHE MUPATAIE</h1>
                            <p>FdR</p>
                        </div>
                    </div>
                    <div className="descriptive-text">
                        <p>
                            Hi, I'm Syntiche. This is my portfolio site
                            showing what I do as a front end developer. Feel 
                            free to look around, learn more about me and chat with me.
                        </p>
                    </div>
                    <div className="hero-btn">
                        <div className="btn-wrapper">
                                <button onClick={scroll} className="button learn">
                                Learn More
                            </button>
                        </div>
                        <div className="btn-wrapper">
                        <button onClick={scrollon} className="button chat">
                            Let's chat
                        </button>
                        </div>
                    </div>
                </div>
                <div className="hero-img">
                    <img className="design-guy" src={hero} alt="Hero" />
                </div>
                </div>
                </div>
            <ProjectHomeFeed />
            <About />
            <Contact />
            <Footer />
        </div>
    )
}